<template>
  <div style="width: 690px;">
    <div class="password-wrapper" :class="[loginMethod === 'password' ? 'bordered' : '']">
      <text class="password-hint">{{loginMethod === 'password' ? '8-16位密码' : '6位数字验证码'}}</text>
      <input v-if="loginMethod === 'password'" class="password" v-model="password" type="password" />
      <div v-else class="sns-code-wrapper">
        <template v-if="inputComponent === 'input'">
          <input
            v-for="index in snsCode.length"
            :style="{borderColor: focusIndex === index - 1 ? '#ff9300' : '#aaa'}"
            class="sns-input"
            ref="snsInput"
            type="number"
            :maxlength="1"
            :value="snsCode[index - 1]"
            @input="handleSnsInput($event, index)"
            @blur="blurSnsInput"
            @focus="focusSnsInput(index)"
            :key="index" />
        </template>
        <template v-else>
          <enhanced-input
            v-for="index in snsCode.length"
            :style="{borderColor: focusIndex === index - 1 ? '#ff9300' : '#aaa'}"
            class="sns-input"
            ref="snsInput"
            type="number"
            :maxlength="1"
            :value="snsCode[index - 1]"
            @input="handleSnsInput($event, index)"
            @blur="blurSnsInput"
            @focus="focusSnsInput(index)"
            @backspace="handleBackspace(index - 1)"
            :key="index" />
        </template>
      </div>
    </div>
    <div class="action">
      <text class="resend" style="margin-top: 20px;" v-if="loginMethod === 'snsCode'" @click="resendSns">{{resendSnsTip}}</text>
      <text class="login-method" style="margin-top: 20px;" @click="toggleLoginMethod">{{loginMethod === 'password' ? '使用验证码登录' : '使用密码登录'}}</text>
    </div>
    <div
      v-if="loginMethod === 'password'"
      class="next-btn"
      @click="attemptPasswordLogin"
    >
      <text class="next-btn-text">确认</text>
    </div>
  </div>
</template>
<script>
import { WxcButton, Utils } from 'weex-ui'
const modal = weex.requireModule('modal')
let unregisterListeners = []
export default {
  name: 'Authorize',
  props: {
    loginMethod: {
      type: String,
      default: 'snsCode'
    }
  },
  watch: {
    loginMethod: {
      handler (value) {
        this.password = ''
        this.snsCode = ['', '', '', '', '', '']
        this.focusIndex = -1
        if (Utils.env.isWeb() && unregisterListeners.length) {
          this.$refs.snsInput.forEach((input, index) => {
            input.$el.removeEventListener('keydown', unregisterListeners[index])
          })
          unregisterListeners = []
        }
        if (Utils.env.isWeb() && value === 'snsCode') {
          this.$nextTick(() => {
            this.listenBackspaceEvents()
          })
        }
      },
      immediate: true
    }
  },
  computed: {
    resendSnsTip () {
      if (this.sendSnsCountdown > 0) {
        return `${this.sendSnsCountdown}秒后重新发送`
      } else {
        return '重新发送'
      }
    }
  },
  data () {
    return {
      password: '',
      snsCode: ['', '', '', '', '', ''],
      focusIndex: -1,
      inputComponent: 'input',
      sendSnsCountdown: 60
    }
  },
  created () {
    if (Utils.env.isWeb()) {
      this.inputComponent = 'input'
    } else {
      this.inputComponent = 'enhanced-input'
    }
  },
  methods: {
    startCountdown () {
      if (this.interval > 0) {
        clearInterval(this.interval)
      }
      this.sendSnsCountdown = 60
      this.interval = setInterval(() => {
        this.sendSnsCountdown -= 1
        if (this.sendSnsCountdown <= 0) {
          clearInterval(this.interval)
          this.interval = -1
        }
      }, 1000)
    },
    isCountdownInProgress () {
      return this.interval > 0
    },
    stopCountdown () {
      if (this.interval > 0) {
        clearInterval(this.interval)
        this.interval = -1
      }
    },
    resendSns () {
      if (this.sendSnsCountdown <= 0) {
        this.$emit('resendSns')
      }
    },
    listenBackspaceEvents () {
      let vm = this
      this.$refs.snsInput.forEach((input, index) => {
        input.$el.addEventListener('keydown', unregisterListeners[index] = event => {
          if (event.keyCode === 8 && !event.target.value && index - 1 >= 0) {
            vm.snsCode[index - 1] = ''
            vm.$refs.snsInput[index - 1].focus()
          }
        })
      })
    },
    handleBackspace (index) {
      if ((!this.snsCode[index]) && index - 1 >= 0) {
        this.snsCode[index - 1] = ''
        this.$refs.snsInput[index - 1].focus()
      }
    },
    blurSnsInput () {
      this.focusIndex = -1
    },
    focusSnsInput (index) {
      let focusIndex = this.snsCode.findIndex(item => item === '')
      if (focusIndex < 0) {
        focusIndex = this.snsCode.length - 1
      }
      this.$refs.snsInput[focusIndex].focus()
      this.focusIndex = focusIndex
    },
    attemptPasswordLogin () {
      // TODO
    },
    handleSnsInput ({value}, index) {
      if (value.length <= 1 || index !== this.snsCode.length) {
        this.snsCode[index - 1] = value
      } else {
        this.$refs.snsInput[index - 1].blur()
        this.$refs.snsInput[index - 1].focus()
      }
      if (value && index < this.snsCode.length) {
        this.$refs.snsInput[index].focus()
      }
    },
    toggleLoginMethod () {
      this.$emit('toggleLoginMethod')
    }
  },
  components: {
    WxcButton
  }
}
</script>
<style scoped>
.password-hint {
  color: #777;
  font-size: 28px;
}
.password-wrapper {
  padding-bottom: 15px;
}
.next-btn {
  height: 88px;
  justify-content: center;
  align-items: center;
  background-color: #ff9300;
  width: 680px;
  margin-top: 140px;
  border-radius: 12px;
}
.next-btn-text {
  color: #fff;
  font-size: 36px;
}
.bordered {
  border-bottom-style: solid;
  border-bottom-width: 2px;
  border-bottom-color: #333;
}
.password {
  margin-top: 15px;
  font-size: 38px;
  outline: none;
}
.action {
  flex-direction: row;
  justify-content: space-between;
}
.login-method, .resend {
  color: #ff9300;
  font-size: 26px;
}
.sns-code-wrapper {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 690px;
  margin-top: 15px;
}
.sns-input {
  text-align: center;
  width: 96px;
  height: 96px;
  border-style: solid;
  border-width: 2px;
  font-size: 36px;
  font-weight: 500;
  outline: none;
  margin: 0 10px;
}
</style>



