import queryString from 'query-string'

const stream = weex.requireModule('stream') || {}
const modal = weex.requireModule('modal') || {}

let basicFetchOptions = {}

function sleep (timeout) {
  return new Promise((resolve) => {
    setTimeout(resolve, timeout)
  })
}

function getRequestPath (url) {
  if (url.startsWith('http')) {
    return url
  }
  return url
}

function fetch (path, config) {
  return new Promise((resolve, reject) => {
    stream.fetch({
      ...config,
      url: path,
      type: 'json'
    }, function (ret) {
      if (!ret.ok) {
        reject(new Error('网络异常'))
      } else {
        resolve(ret.data)
      }
    })
  })
}

const getQueryStringUrl = (url, data) => {
  let query = ''
  const queryMap = {
    ...queryString.parse(url.indexOf('?') > -1 ? url.substring(url.indexOf('?')) : ''),
    ...data
  }
  const host = url.indexOf('?') > -1 ? url.substring(0, url.indexOf('?')) : url
  if (Object.keys(queryMap).length) {
    query = `?${queryString.stringify(queryMap)}`
  }
  return `${host}${query}`
}

const sendRequest = async (config) => {
  let response = null
  let {
    method = 'GET',
    data,
    url: requestPath,
    credentials
  } = config

  requestPath = getRequestPath(requestPath)
  method = method.toUpperCase()
  if (credentials) {
    basicFetchOptions['credentials'] = credentials
  }

  switch (method) {
    case 'GET':
      requestPath = getQueryStringUrl(requestPath, data)
      try {
        response = await fetch(requestPath, {
          method,
          ...basicFetchOptions
        })
      } catch (e) {
        modal.toast({
          message: e.message,
          duration: 3
        })
      }
      break
    case 'POST':
    case 'PUT':
    case 'DELETE':
      try {
        response = await fetch(requestPath, {
          ...basicFetchOptions,
          method,
          body: JSON.stringify(data),
          headers: {
            ...basicFetchOptions.headers,
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          }
        })
      } catch (e) {
        modal.toast({
          message: e.message,
          duration: 3
        })
      }
      break
    default:
      break
  }

  return response
}

const timeoutPromise = async (timeout) => {
  await sleep(timeout)
  throw new Error('网络超时')
}

const getResult = async (timeout, libConfig = {}) => {
  try {
    let result = await Promise.race([timeoutPromise(timeout, libConfig), sendRequest(libConfig)])

    // 返回 null 的情况，在 sendRequest 中已经上报过错误了
    if (!result) {
      return null
    }
    return result
  } catch (e) {
    // should toast errorMessage if any
    modal.toast({
      message: e.message,
      duration: 3
    })
    return null
  }
}

/**
 * 发起AJAX请求
 * @method
 * @name fetch
 * @param {fetchOptions} config 参数
 * @param {number} [config.timeout=60000] 超时时间
 * @param {string} [config.method=GET] 请求方法
 * @param {*} [config.data] 请求数据
 * @param {string} config.url 请求URL
 * @param {boolean} [config.credentials] 是否增加认证参数，参考fetch方法的credentials
 * @param {boolean} [config.rawResponse] 是否返回完整的response对象，默认false
 */
export default async (config) => {
  const {
    timeout = 60000,
    ...libConfig
  } = config
  const resp = await getResult(timeout, libConfig)

  // resp 为 false， 代表 getResult 返回了 null, 即已经将错误在 getResult 方法内上报，此处直接 return
  if (!resp || typeof resp !== 'object') {
    return null
  }

  const respSuccess = resp['ret'] === 200

  if (config.rawResponse) {
    return resp
  }
  if (!respSuccess) {
    throw new Error(typeof resp.message === 'undefined' ? resp.msg : resp.message)
  } else {
    return resp.data
  }
}



