`<template>
  <div class="welcome-wrapper" :style="wrapperStyle">
    <div class="decorator"/>
    <image :src="backBtnSrc" @click="back" class="back-btn" :style="{opacity: step === 1 ? 0 : 1}" />
    <div class="upper" ref="welcomeUpper">
      <div class="upper-container" ref="upperOne">
        <text class="welcome-title-main">请输入手机号码</text>
        <text class="welcome-title-desc">为了方便进行联系，请输入您的常用手机号码</text>
      </div>
      <div class="upper-container hidden" ref="upperTwo">
        <text class="welcome-title-main">{{loginMethod === 'password' ? '请您输入登录密码' : '请您输入验证码'}}</text>
        <text class="welcome-title-desc" v-if="loginMethod === 'password'">请使用账号密码登录</text>
        <text class="welcome-title-desc" v-else>验证码已经发送到：{{region}} {{phone | friendlyPhone}}</text>
      </div>
    </div>
    <div class="frame">
      <div class="frame-wrapper" ref="welcomeFrame">
        <Welcome @nextStep="inputPhoneAndRegion" @showAgreement="toggleShowAgreement" />
        <Authorize ref="authorize" :login-method="loginMethod" @toggleLoginMethod="toggleLoginMethod" @resendSns="sendSns" />
      </div>
    </div>
    <wxc-mask height="800"
              width="702"
              border-radius="0"
              duration="200"
              mask-bg-color="#FFFFFF"
              :has-animation="false"
              :has-overlay="true"
              :overlay-can-close="false"
              :show-close="true"
              :show="showAgreement"
              @wxcMaskSetHidden="toggleShowAgreement">
      <part-loading v-if="isWebEnv" class="loading" :width="200" :height="200" :show="true"></part-loading>
      <web style="width: 702px;height: 800px;" src="https://pizza-express-app-1252752952.cos.ap-shanghai.myqcloud.com/%E8%B5%84%E8%B4%A8%E4%B8%8E%E8%A7%84%E5%88%99.html" />
    </wxc-mask>
  </div>
</template>

<script>
import { Utils, WxcMask } from 'weex-ui'
import Util from '@/utils'
import PartLoading from '@/common/PartLoading'
import Welcome from './components/Welcome'
import Authorize from './components/Authorize'
const statusBarChanger = weex.requireModule('StatusBarChanger')
const nativeNavigator = weex.requireModule('NativeNavigator')
const animation = weex.requireModule('animation')
const globalEvent = weex.requireModule('globalEvent')
export default {
  name: 'App',
  created () {
    if (Utils.env.isAndroid()) {
      statusBarChanger.enableFullscreen()
      globalEvent.addEventListener('AndroidBack', () => {
        if (this.step > 1) {
          this.back()
        } else if (this.showAgreement) {
          this.showAgreement = false
        } else {
          nativeNavigator.pop()
        }
      })
    }
  },
  data () {
    return {
      step: 1,
      region: '',
      phone: '',
      loginMethod: 'password',
      backBtnSrc: Util.getImg('back_light.png'),
      showAgreement: false,
      isWebEnv: Utils.env.isWeb()
    }
  },
  computed: {
    wrapperStyle () {
      const deviceStatusBarHeight = +(weex.config.env.statusBarHeight || 0)
      return {
        paddingTop: `${deviceStatusBarHeight * 750 / (+weex.config.env.deviceWidth)}px`
      }
    }
  },
  filters: {
    friendlyPhone (value) {
      const first = value.substring(0, 3)
      const middle = value.substring(3, 7)
      const last = value.substring(7, 11)
      return `${first}${middle ? ' ' : ''}${middle}${last ? ' ' : ''}${last}`
    }
  },
  methods: {
    toggleShowAgreement () {
      this.showAgreement = !this.showAgreement
      if (this.showAgreement) {
        this.isAgreementLoading = true
      }
    },
    sendSns () {
      // TODO
      this.$refs.authorize.startCountdown()
    },
    back () {
      if (Utils.env.isAndroid()) {
        nativeNavigator.hideKeyboard()
      }
      this.step = 1
      animation.transition(this.$refs.welcomeFrame, {
        styles: {
          transform: `translateX(0px)`
        },
        duration: 300,
        timingFunction: 'cubic-bezier(0.25, 0.46, 0.45, 0.94)',
        delay: 0
      }, () => {})
      animation.transition(this.$refs.upperOne, {
        styles: {
          opacity: 1
        },
        duration: 300,
        timingFunction: 'cubic-bezier(0.25, 0.46, 0.45, 0.94)',
        delay: 0
      })
      animation.transition(this.$refs.upperTwo, {
        styles: {
          opacity: 0
        },
        duration: 300,
        timingFunction: 'cubic-bezier(0.25, 0.46, 0.45, 0.94)',
        delay: 0
      })
    },
    inputPhoneAndRegion ({region, phone}) {
      if (Utils.env.isAndroid()) {
        nativeNavigator.hideKeyboard()
      }
      this.region = region
      this.phone = phone
      animation.transition(this.$refs.welcomeFrame, {
        styles: {
          transform: `translateX(${-690}px)`
        },
        duration: 300,
        timingFunction: 'cubic-bezier(0.25, 0.46, 0.45, 0.94)',
        delay: 0
      }, () => {
        this.step = 2
      })
      animation.transition(this.$refs.upperOne, {
        styles: {
          opacity: 0
        },
        duration: 300,
        timingFunction: 'cubic-bezier(0.25, 0.46, 0.45, 0.94)',
        delay: 0
      })
      animation.transition(this.$refs.upperTwo, {
        styles: {
          opacity: 1
        },
        duration: 300,
        timingFunction: 'cubic-bezier(0.25, 0.46, 0.45, 0.94)',
        delay: 0
      })
    },
    toggleLoginMethod () {
      if (Utils.env.isAndroid()) {
        nativeNavigator.hideKeyboard()
      }
      this.loginMethod = this.loginMethod === 'snsCode' ? 'password' : 'snsCode'
      if (this.$refs.authorize.isCountdownInProgress() === false) {
        this.$refs.authorize.startCountdown()
      }
    }
  },
  components: {
    Welcome,
    Authorize,
    WxcMask,
    PartLoading
  }
}
</script>

<style scoped>
.welcome-wrapper {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 750px;
  background-color: #fff;
}
.loading {
  position: absolute;
  left: 251px;
  top: 300px;
}
.back-btn {
  width: 21px;
  height: 36px;
  margin-top: 27px;
  margin-left: 32px;
}
.hidden {
  opacity: 0;
}
.upper {
  position: absolute;
  top: 160px;
  width: 750px;
  height: 180px;
  padding: 0 30px;
}
.upper-container {
  position: absolute;
  left: 30px;
  right: 30px;
  top: 0;
  bottom: 0;
}
.decorator {
  background-color: rgb(255, 241, 0);
  position: absolute;
  right: -375px;
  top: -375px;
  width: 750px;
  height: 750px;
  border-radius: 375px;
}
.welcome-title-main {
  font-weight: 500;
  font-size: 38px;
  padding: 20px 0;
}
.welcome-title-desc {
  font-size: 28px;
  color: #777;
}
.frame {
  position: absolute;
  top: 340px;
  left: 30px;
  right: 30px;
  bottom: 0;
  overflow: hidden;
}
.frame-wrapper {
  width: 1380px;
  flex-direction: row;
}
</style>



