<template>
  <div>
    <image :src="PART"
           v-if="show"
           :style="loadingStyle"
           resize="contain"
           quality="original"></image>
  </div>
</template>

<script>
import Util from '@/utils'
export default {
  name: 'PartLoading',
  props: {
    show: {
      type: Boolean,
      default: false
    },
    width: {
      type: [Number, String],
      default: 36
    },
    height: {
      type: [Number, String],
      default: 36
    }
  },
  data: () => ({
    PART: Util.getImg('loading.gif')
  }),
  computed: {
    loadingStyle () {
      const { height, width } = this
      return {
        height: `${height}px`,
        width: `${width}px`
      }
    }
  }
}
</script>


