<template>
  <div style="width: 690px;">
    <div class="input-line">
      <text class="region">{{region}}</text>
      <input class="phone-input" :value="phone" @input="phoneInput" type="tel" placeholder="请输入手机号码" :maxlength="13" />
    </div>
    <div class="user-license">
      <image :src="checkBoxSrc" class="check-box" @click="toggleAgreeLicense" />
      <text class="small-desc">同意</text>
      <text class="small-desc" style="color: #2395ff; cursor: pointer; margin-left: 10px;" @click="toggleShowUserAgreement">用户协议</text>
    </div>
    <div
      class="next-btn"
      @click="nextStep"
    >
      <text class="next-btn-text">下一步</text>
    </div>
    <div class="third-app">
      <div class="bottom-section">
        <div class="bottom-section-line"></div>
        <text class="bottom-section-text">其他方式登录</text>
        <div class="bottom-section-line"></div>
      </div>
      <div class="app-row">
        <div class="third-app-item">
          <image :src="alipayIcon" class="third-app-icon" />
          <text class="third-app-name">支付宝</text>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { WxcButton } from 'weex-ui'
import Util from '@/utils'
const modal = weex.requireModule('modal')
const CHECKBOX_SRC_MAP = {
  uncheck: Util.getImg('checkbox_uncheck.png'),
  check: Util.getImg('checkbox_check.png')
}
export default {
  name: 'Welcome',
  data () {
    return {
      phone: '',
      region: '+86',
      agree: false,
      alipayIcon: Util.getImg('alipay_circle.png')
    }
  },
  computed: {
    checkBoxSrc () {
      return CHECKBOX_SRC_MAP[this.agree ? 'check' : 'uncheck']
    },
    formValid () {
      return this.agree && this.phone.replace(/\s+/g, '').length === 11
    }
  },
  methods: {
    phoneInput ({value}) {
      if (!value) {
        return
      }
      if (value.length > this.phone.length) {
        const rawInput = value.replace(/\s+/g, '')
        const first = rawInput.substring(0, 3)
        const middle = rawInput.substring(3, 7)
        const last = rawInput.substring(7, 11)
        this.phone = `${first}${middle ? ' ' : ''}${middle}${last ? ' ' : ''}${last}`
      } else {
        if (value.length === 4 || value.length === 9) {
          this.phone = value.substring(0, value.length - 1)
        } else {
          this.phone = value
        }
      }
    },
    toggleShowUserAgreement () {
      this.$emit('showAgreement')
    },
    nextStep () {
      if (this.formValid) {
        this.$emit('nextStep', {
          region: this.region,
          phone: this.phone.replace(/\s+/g, '')
        })
      } else if (this.phone.replace(/\s+/g, '').length !== 11) {
        modal.toast({
          message: '请输入中国大陆11位手机号',
          duration: 2
        })
      } else if (this.agree === false) {
        modal.toast({
          message: '请同意用户协议',
          duration: 2
        })
      }
    },
    toggleAgreeLicense () {
      this.agree = !this.agree
    }
  },
  components: {
    WxcButton
  }
}
</script>
<style scoped>
.check-box {
  width: 40px;
  height: 40px;
  margin-right: 20px;
}
.input-line {
  padding: 10px 0;
  border-bottom-style: solid;
  border-bottom-width: 2px;
  border-bottom-color: #777;
  flex-direction: row;
}
.next-btn {
  height: 88px;
  justify-content: center;
  align-items: center;
  background-color: #ff9300;
  width: 680px;
  margin-top: 140px;
  border-radius: 12px;
}
.next-btn-text {
  color: #fff;
  font-size: 36px;
}
.region {
  background-color: #eee;
  border-radius: 4px;
  padding: 10px 16px;;
  font-size: 28px;
  margin-right: 30px;
}
.phone-input {
  flex: 1;
  background-color: transparent;
  font-size: 36px;
  outline: none;
}
.user-license {
  flex-direction: row;
  margin-top: 20px;
  align-items: center;
}
.small-desc {
  font-size: 22px;
}
.bottom-section {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100px;
}
.bottom-section-text {
  color: #888;
  font-size: 24px;
}
.bottom-section-line {
  background-color: #888;
  width: 200px;
  height: 1px;
  margin: 0 20px;
}
.app-row {
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.third-app {
  margin-top: 70px;
}
.third-app-icon {
  width: 60px;
  height: 60px;
}
.third-app-item {
  align-items: center;
}
.third-app-name {
  color: #777;
  margin-top: 10px;
  font-size: 22px;
}
</style>



