import { Utils } from 'weex-ui'
const locationManager = weex.requireModule('LocationManager')
let loaded = false
let firstRunning = false
let pendingRequests = []
let locationObj = {
  lat: 0,
  lng: 0
}

function onLocationGet () {
  firstRunning = false
  loaded = true
  pendingRequests.forEach(req => {
    const { resolve } = req
    resolve(locationObj)
  })
  pendingRequests = []
}

async function getLocation () {
  if (loaded) {
    return locationObj
  } else if (firstRunning) {
    return new Promise((resolve, reject) => {
      pendingRequests.push({resolve, reject})
    })
  }
  firstRunning = true
  return new Promise((resolve, reject) => {
    if (Utils.env.isWeb()) {
      h5GetLocation(resolve, reject)
    } else {
      locationManager.getLocation(({success, latitude, longitude, message}) => {
        if (success !== 'true') {
          reject(new Error(`定位失败！${message}`))
          return
        }
        locationObj.lat = latitude
        locationObj.lng = longitude
        resolve(locationObj)
        onLocationGet()
      })
    }
  })
}

function h5GetLocation (resolve, reject) {
  if (navigator.geolocation) {
    navigator.geolocation.getCurrentPosition(pos => {
      [locationObj.lat, locationObj.lng] = [pos.coords.latitude, pos.coords.longitude]
      resolve(locationObj)
      onLocationGet()
    }, () => {
      reject()
      onLocationGet()
    })
  } else {
    reject()
    onLocationGet()
  }
}

try {
  getLocation()
} catch (e) {
}

export default getLocation



